import {Component, Inject, Input} from '@angular/core';
import {FormUtilitiesOptions} from '../../../form-utilities.module';

@Component({
  moduleId: module.id,
  selector: 'bd-form-label',
  template: `<label class="{{ options.classFormLabel }}" for="{{ inputId }}" *ngIf="label">{{ label }}</label>
`
})
export class FormLabelComponent {

  @Input()
  label: string;

  @Input()
  inputId: string;

  public constructor(@Inject('options') public _options: FormUtilitiesOptions) {
  }

  get options(): FormUtilitiesOptions {
    return this._options;
  }
}
